(function() {
    'use strict';

    angular
        .module('web')
        .controller('TemplateInputSideController', TemplateInputSideController);

    /** @ngInject */
    function TemplateInputSideController($scope, side, AccountService, AccountUnitService, AccountBalanceService, InventoryChildService, $uibModal, $uibModalInstance) {

        $scope.side = side;
        $scope.create = create;
        $scope.cancel = cancel;
        $scope.onAccountSelected = onAccountSelected;
        $scope.onAccountChildSelected = onAccountChildSelected;
        $scope.onTypeSelected = onTypeSelected;
        $scope.useSubAccount = useSubAccount;



        function reload() {

            $scope.isMultiple = false;

            $scope.accounts = [];

            var accounts = AccountService.query({
                'side': side,
                'level': 3
            }, null, function() {
                for (var i = 0; i < accounts.length; i++) {
                    $scope.accounts.push({
                        id: accounts[i].id,
                        code: accounts[i].code,
                        name: accounts[i].name,
                        isInventory: accounts[i].accountType === 'CHILD_AS_INVENTORY',
                        hasChild: accounts[i].accountType !== 'FINAL_ACCOUNT',
                        amount: 0,
                        childCode: '',
                        type: 1,
                        quantity: 1
                    });
                }
            });

        }

        reload();

        function onAccountSelected() {

            $scope.unitName = '';
            $scope.type = null;
            $scope.accountChilds = [];

            var accountChilds = AccountService.query({
                'accountId': $scope.selectedAccount.id

            }, null, function() {
                for (var i = 0; i < accountChilds.length; i++) {
                    $scope.accountChilds.push({
                        id: accountChilds[i].id,
                        code: accountChilds[i].code,
                        name: accountChilds[i].name,
                        isInventory: accountChilds[i].accountType === 'CHILD_AS_INVENTORY',
                        hasChild: accountChilds[i].accountType == 'FINAL_ACCOUNT',
                        amount: 0,
                        childCode: '',
                        type: 1,
                        quantity: 1
                    });
                }
            });

        }

        function useSubAccount() {
            if (!$scope.isUseSubAccount) {
                $scope.selectedAccountChild = null;
            } else {
                $scope.isMultiple = false;
            }
        }

        function onAccountChildSelected() {

            $scope.selectedAccount.amount = 0;
            $scope.selectedAccount.quantity = 1;

            if ($scope.selectedAccountChild && $scope.selectedAccountChild.isInventory) {
                var accountUnit = AccountUnitService.get({
                    'accountId': $scope.selectedAccountChild.id
                }, null, function() {
                    if (accountUnit.unit) {
                        $scope.unitName = accountUnit.unit.name;
                    }
                });
                onTypeSelected();
            }

        }

        function onTypeSelected() {

            if ($scope.type == -1) {
                if ($scope.selectedAccount.isInventory && $scope.selectedAccountChild) {

                    var inventory = InventoryChildService.get({
                        'accountId': $scope.selectedAccountChild.id

                    }, null, function() {

                        if (typeof inventory === 'undefined') {
                            $scope.selectedAccount.amount = 0;

                        } else {
                            $scope.selectedAccount.amount = inventory.balancePrice;
                        }

                    });

                }

            } else {
                $scope.selectedAccount.amount = 0;
            }

        }


        function create() {

            if($scope.useSubAccount==true && !$scope.selectedAccountChild){
                alert('Account child must selected');
                return;
            }

            var result = {
                type: $scope.type,
                name: $scope.selectedAccountChild ? $scope.selectedAccountChild.name : $scope.selectedAccount.name,
                code: $scope.selectedAccountChild ? $scope.selectedAccountChild.code : $scope.selectedAccount.code,
                side: side,
                hasChild: $scope.selectedAccountChild? false : $scope.selectedAccount.hasChild,
                isMultiple : $scope.isMultiple,
                variable: $scope.variable,
                expression: $scope.expression
            };
            closeModal(result);
        }

        function closeModal(data) {
            $uibModalInstance.close(data);
        };

        function cancel() {
            $uibModalInstance.dismiss();
        };

    }

})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('TemplateInputController', TemplateInputController);

    /** @ngInject */
    function TemplateInputController($scope, $uibModal, AccountService, TemplateService, $uibModalInstance) {

        $scope.createSide = createSide;
        $scope.createJournal = createJournal;

        $scope.journals = [];

        $scope.removeJournal = removeJournal;

        $scope.isDebet = isDebet;

        $scope.isCredit = isCredit;

        function createSide(side) {
            $uibModal.open({
                templateUrl: 'app/accounting/accounting/template/input/template-input-side.html',
                controller: 'TemplateInputSideController',
                resolve: {
                    side: function() {
                        return side;
                    }
                }
            }).result.then(function(data) {
                $scope.journals.push(data);
            });
        }

        function removeJournal(index) {
            $scope.journals.splice(index, 1);
        }

        function createJournal() {

            if(typeof $scope.description === 'undefined' || $scope.description ===''){
                alert('description masih kosong. Silakan diisi terlebih dahulu');
                return;
            }

            var transactions = [];

            for (var i = 0; i < $scope.journals.length; i++) {
                transactions.push({
                    code: $scope.journals[i].code,
                    multiple: $scope.journals[i].isMultiple,
                    expression: $scope.journals[i].expression,
                    variable:$scope.journals[i].variable,
                    type:$scope.journals[i].type                    
                });
            }

            TemplateService.save({
                description: $scope.description,
                accounts: transactions
            }, closeModal);

        }

        function closeModal() {
            $uibModalInstance.close();
        };

        function cancel() {
            $uibModalInstance.dismiss();
        };

        function isDebet(journal) {
            return (journal.side === 'Activa' && journal.type > 0) || (journal.side ==='Passiva' && journal.type < 0);
        }

        function isCredit(journal) {
            return (journal.side === 'Passiva' && journal.type > 0) || (journal.side ==='Activa' && journal.type < 0);
        }

    }

})();

(function() {
    'use strict';

    angular
        .module('web')
        .controller('JournalInputSideController', JournalInputSideController);

    /** @ngInject */
    function JournalInputSideController($scope, side, AccountService, AccountUnitService, AccountBalanceService, InventoryChildService, $uibModal, $uibModalInstance) {

        $scope.side = side;
        $scope.create = create;
        $scope.cancel = cancel;
        $scope.onAccountSelected = onAccountSelected;
        $scope.onAccountChildSelected = onAccountChildSelected;
        $scope.onTypeSelected = onTypeSelected;

        function reload() {

            $scope.accounts = [];

            var accounts = AccountService.query({
                'side': side,
                'level': 3
            }, null, function() {
                for (var i = 0; i < accounts.length; i++) {
                    $scope.accounts.push({
                        id: accounts[i].id,
                        code: accounts[i].code,
                        name: accounts[i].name,
                        isInventory: accounts[i].accountType === 'CHILD_AS_INVENTORY',
                        hasChild: accounts[i].accountType !== 'FINAL_ACCOUNT',
                        amount: 0,
                        childCode: '',
                        type: 1,
                        quantity: 1
                    });
                }
            });

        }

        reload();

        function onAccountSelected() {

            $scope.unitName = '';
            $scope.type = null;
            $scope.accountChilds = [];

            var accountChilds = AccountService.query({
                'accountId': $scope.selectedAccount.id

            }, null, function() {
                for (var i = 0; i < accountChilds.length; i++) {
                    $scope.accountChilds.push({
                        id: accountChilds[i].id,
                        code: accountChilds[i].code,
                        name: accountChilds[i].name,
                        isInventory: accountChilds[i].accountType === 'CHILD_AS_INVENTORY',
                        hasChild: accountChilds[i].accountType == 'FINAL_ACCOUNT',
                        amount: 0,
                        childCode: '',
                        type: 1,
                        quantity: 1
                    });
                }
            });

        }

        function onAccountChildSelected() {

            $scope.selectedAccount.amount = 0;
            $scope.selectedAccount.quantity = 1;

            if ($scope.selectedAccountChild && $scope.selectedAccountChild.isInventory) {
                var accountUnit = AccountUnitService.get({
                    'accountId': $scope.selectedAccountChild.id
                }, null, function() {
                    if (accountUnit.unit) {
                        $scope.unitName = accountUnit.unit.name;
                    }
                });
                onTypeSelected();
            }

        }

        function onTypeSelected() {

            if ($scope.type == -1) {
                if ($scope.selectedAccount.isInventory && $scope.selectedAccountChild) {

                    var inventory = InventoryChildService.get({
                        'accountId': $scope.selectedAccountChild.id
                        
                    }, null, function() {

                        if (typeof inventory === 'undefined') {
                            $scope.selectedAccount.amount = 0;

                        } else {
                            $scope.selectedAccount.amount = inventory.balancePrice;
                        }

                    });

                }

            } else {
                $scope.selectedAccount.amount = 0;
            }

        }


        function create() {

            var desc = '';

            if ($scope.selectedAccount.isInventory) {
                desc = $scope.selectedAccount.name + ' : ' + $scope.selectedAccountChild.name + ' (' + $scope.selectedAccount.quantity + ' ' + $scope.unitName + ') @Rp ' + $scope.selectedAccount.amount;

            } else if ($scope.selectedAccountChild) {
                desc = $scope.selectedAccount.name + ' : ' + $scope.selectedAccountChild.name;

            } else {
                desc = $scope.selectedAccount.name;
            }

            var result = {
                quantity: $scope.selectedAccount.quantity * $scope.type,
                amount: $scope.selectedAccount.amount,
                totalAmount: $scope.selectedAccount.amount * $scope.selectedAccount.quantity,
                code: $scope.selectedAccountChild ? $scope.selectedAccountChild.code : $scope.selectedAccount.code,
                desc: desc,
                side: side
            };
            closeModal(result);
        }

        function closeModal(data) {
            $uibModalInstance.close(data);
        };

        function cancel() {
            $uibModalInstance.dismiss();
        };

    }

})();

